<template>
  <div class="slider-container">
    <div class="slider-liner disabled" v-show="disabled"></div>
    <div class="slider-liner"
        :id="oboxID"
        v-show="!disabled"
        @click="clickBox"
        :style="{
      background: `linear-gradient(to right, ${active} ${value * 100}%, ${background} ${value * 100}%)`
    }">
      <div class="slider-pointer"
          :id="odragID"
          @mousedown="dragStart"
          :style="{
              left: maxWidth * value + 'px',
              background: active,
            }">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'slider',
  props: {
    /**
     * 
     */
    active: {
      type: String,
      default: '#20a0ff'
    },
    /**
     * 
     */
    background: {
      type: String,
      default: '#ddd'
    },
    /**
     * 
     */
    value: {
      type: [Number, String],
      default: 0,
    },
    /**
     * 
     */
    disabled: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      x: 0,
      odrag: '',
      obox: '',
      maxWidth: 0,
      // idslider
      oboxID: Math.random() * 10000,
      odragID: Math.random() * 100000,
    }
  },
  mounted () {
    this.obox = document.getElementById(this.oboxID)
    this.odrag = document.getElementById(this.odragID)
    this.maxWidth = this.obox.clientWidth - this.odrag.clientWidth
  },
  methods: {
    /**
     * 
     * @param {*} ev dom
     */
    dragStart (ev) {
      this.draging = true
      const x = ev.clientX - this.odrag.offsetLeft
      document.onmousemove = (ev) => {
        // 
        document.getSelection().removeAllRanges()
        // x
        const left = ev.clientX - x
        this.maxWidth = this.obox.clientWidth - this.odrag.clientWidth
        // 0
        const move = Math.min(this.maxWidth, Math.max(left, 0))
        this.updateValue(move / this.maxWidth)
        this.showTip = true
      }

      document.onmouseup = () => {
        document.onmousemove = document.onmouseup = null
      }
    },
    /**
     * 
     * @param {*} ev dom
     */
    clickBox (ev) {
      if (this.disabled) return
      if (!ev.target.className.includes('slider-liner')) return
      const left = ev.offsetX - this.odrag.clientWidth / 2
      this.updateValue(left / this.maxWidth)
    },
    /**
     * 
     * @param {number} rate 
     */
    updateValue (rate) {
      // 
      /**
       * 
       * @event input
       * @type {string}
       */
      this.$emit('input', Number(rate).toFixed(2))
      /**
       * 
       * @event change
       * @type {string}
       */
      this.$emit('change', rate)
    },
  },
}
</script>
<style lang="stylus" scoped>
.slider{
  &-container{
    position relative
    width 100%
    padding 10px 0
  }
  &-liner{
    position relative
    width 100%;
    height: 2px;
    border-radius: 2px;
    cursor: pointer
    &.disabled {
      background #ccc
    }
  }
  &-pointer{
    width 10px
    height 10px
    top: -4px;
    border-radius: 100%;
    background: #000;
    position: absolute;
    cursor: pointer
  }
  &-tips{
    position absolute
    left 50%
    top -20px
    transform translateX(-50%)
    line-height 20px
  }
}
</style>
